* {
    box-sizing: border-box;
    font-family: 'PT Sans', sans-serif;
}

body {
    margin: 0;
    padding: 0;
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 80px);
    width: 100%;
    filter: blur(1px);
}

.min-height {
    min-height: 92vh;
}